@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.mr-20 {
  margin-right: 20px !important;
}
#header {
  display: flex;
  width: 150px;
 
}
#header .pro-sidebar {
  height: 100%;
}

#header .pro-sidebar {
  width: 100%;
  min-width: 100%;
}
#header .pro-sidebar-inner {
  background: linear-gradient(
    
    rgba(69, 20, 198, 1) 0%,
    rgba(94, 51, 207, 1) 50%,
    rgb(209, 194, 248) 99%,
    rgba(255, 255, 255, 1) 100%,
    rgba(255, 255, 255, 1) 100%
  );
  box-shadow: 0.5px 0.866px 2px 0px rgba(0, 0, 0, 0.15);
  height: 100%;
}
#header .pro-sidebar-inner .pro-sidebar-layout {
  overflow-y: hidden;
}
#header .pro-sidebar-inner .pro-sidebar-layout .logotext p {
  font-size: 20px;
  padding: 0 20px;
  color: white;
  font-weight: bold;
}
#header .pro-sidebar-inner .pro-sidebar-layout ul {
  padding: 0 5px;
}
#header .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item {
  color: #000;
  margin: 20px 29px;
  font-weight: bold;
}
#header
  .pro-sidebar-inner
  .pro-sidebar-layout
  ul
  .pro-inner-item
  .pro-icon-wrapper {
  background-color: #fbf4cd;
  color: #000;
  border-radius: 3px;
}
#header
  .pro-sidebar-inner
  .pro-sidebar-layout
  ul
  .pro-inner-item
  .pro-icon-wrapper
  .pro-item-content {
  color: #000;
}
#header .pro-sidebar-inner .pro-sidebar-layout .active:hover {
  background-image: linear-gradient(0deg, goldenrod 0%, goldenrod 100%);
  width: 100%;
  
  
}
#header .logo {
  padding: 20px;
}

@media only screen and (max-width: 720px) {
  html {
    overflow: hidden;
  }
}

.user_pic_ctr {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}

.user_pic_ctr > img {
  width: 70px;
  border-radius: 50%;
  padding-top: 20px;
  padding-bottom: 10px;
  margin-right: 5px;
  
  
}

.side-logo {
  width: 175px;
  margin-top: 150px;
  margin-left: 20%;
}

.side-container:hover .user-name {
  padding: 0px;
  margin: 0px;
}

.user_pic_ctr > p {
  padding: 0px;
  margin: 0px;
}

.react-icons {
  background-color: rgb(0, 0, 0, 0);
  height: 50px;
  width: 22px;
  color: black;
  padding: 0px;
  vertical-align: middle;
  margin: 0px;
}

.side-container:hover #header .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item {
  color: #000;
  margin: 20px 20px;
  font-weight: bold;
}

#logout {
  background: transparent;
  border: none;
  color:rgb(209, 194, 248);
  font-size: 1.0em;
}
.header-ctr {
  width: 100%;
  border-bottom: 1px solid black;
}

.header-ctr > h4 {
  padding-left: 15px;
  padding-top: 10px;
  padding-bottom: 0px;
  margin: 0px;
}

.header-ctr > h2 {
  padding-top: 2px;
  margin: 0px;
  padding-left: 15px;
}

.performance-ctr {
  padding-left: 10px;
  padding-bottom: 30px;
  padding-top: 0px;
  width: 65%;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 60px 40px -7px;
  height: 90%;
  margin: 10px;
}

.performance-ctr > h3 {
  margin-bottom: 0px;
}

debtincome-ctr > h3 {
  padding-top: 20px;
  margin-bottom: 0px;
}

.debtincome-ctr {
  width: 35%;
  /* border: 1px solid purple; */
  height: 90%;
  margin: 10px;
  padding: 0px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 60px 40px -7px;
}

.income-ctr {
  /* border: 1px solid gold; */
  height: 90%;
  padding-left: 10px;
  padding-bottom: 30px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 60px 40px -7px;
  margin: 10px;
  width: 33%;
}

.income-ctr > h3 {
  padding-left: 15px;
  margin-bottom: 0px;
}



.switchbutton {
  background-color: transparent;
  color: goldenrod;
  padding: 0px 5px;
  cursor: pointer;
  font-size: 1em;
  border: none;
  outline: none;
  display: inline-block;
  font-weight: bolder;
}
.switchbutton:hover {
  font-size: 1.3em;
}

.details-main-ctr .formCenter {
  width: 100%;
  height: 350px;
  margin-right: auto;
  margin-top: 15%;
  display: block;
  border: 2px solid goldenrod;
  background-color: black;
  padding-right: 60px;
  padding-left: 60px;
}

.form-title {
  color: white;
  font-weight: 300;
  margin-bottom: 20px;
  border-bottom: 1px solid goldenrod;
  display: inline-block;
}

.landing-title-signup {
  color: white;
  border-bottom: 1px solid goldenrod;
  display: inline-block;
  font-weight: 300;
  padding-bottom: 5px;
}


.formField {
  margin-bottom: 40px;
}

.details-main-ctr .formFieldLabel {
  display: block;
  text-transform: uppercase;
  font-size: 1em;
  color: white;
  padding-top: 10px;
}
.formFieldInput {
  width: 85%;
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  border-bottom: 1px solid goldenrod;
  font-size: 1em;
  font-weight: 300;
  padding-bottom: 10px;
  margin-top: 5px;
}

.formFieldInput:-ms-input-placeholder {
  color: white;
}

.formFieldInput::placeholder {
  color: white;
}

.details-main-ctr .formFieldButton {
  background-color: rgba(69, 20, 198, 1);
  color: white;
  border: 1px solid goldenrod;
  outline: none;
  padding: 15px 50px;
  font-size: 1em;
  font-weight: 300;
  margin-top: 10px;
}

.formFieldButton:hover {
  background-color: linear-gradient(0deg, goldenrod 0%, goldenrod 100%);
}
.assets-ctr {
  border: 1px solid gold;
  height: 90%;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 60px 40px -7px;
  padding-bottom: 35px;
  margin: 10px;
  width: 33%;

}

.assets-ctr > h3 {
  margin-bottom: 0px;
  margin-right: 10px;
}

.debt-ctr {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 60px 40px -7px;
  height: 90%;
  padding-bottom: 40px;
  margin: 10px;
  width: 33%;
}

.debt-ctr > h3 {
  margin-bottom: 0px;
}

.dashboard-body {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  width: 100%;
  height: 700px;
  /* border: 3px solid red; */
}

.side-container {
  border-right: 3px solid goldenrod;
  display: flex;
  top: 0;
  bottom: 0;
  height: 100vh;
  left: 0;
  width: 10%;
  overflow: hidden;
  transition: width 0.05s linear;
  -webkit-transform: translateZ(0) scale(1, 1);
  z-index: 1000;
  margin-right: 0px;
}

.side-container:hover,
.side-container.expanded {
  width: 350px;
  height: 100vh;
  overflow: visible;
  border-right: 6px solid goldenrod;
  transition: width 0.35s linear;
}
.side-container:before > img {
  width: 5px;
}
.main-container {
  display: flex;
  flex-direction: column;
  width: 90%;
  /* border: 1px solid blue; */
  height: 100%;
}

/* .main-container:hover {
width: 78%
} */

.main-row1-ctr {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  /* border: 1px solid blue; */
  height: 50%;
  padding: 0px 20px;
}
.main-row2-ctr {
  display: flex;
  justify-content: space-evenly;
  /* align-content: center; */
  width: 100%;
  height: 50%;
}

.side-container .user-name {
  display: none;
}

.side-container:hover .user-name {
  display: inline;
  padding: 0px;
  margin: 0px;
  color: white;
}

.side-container .user-email {
  display: none;
}

.side-container:hover .user-email {
  display: inline;
  padding: 0px;
  margin: 0px;
}

.side-container .sidebar-links {
  display: none;
}

.side-container:hover .sidebar-links {
  display: inline;
}
.side-container .side-logo {
  width: 100px;
  margin-left: 10px;
}

.side-container:hover .side-logo {
  margin-top: 0px;
  width: 200px;
}

.side-container:hover #header {
  width: 225px;
  height: 100vh;
}

